
h1 {
  font-size: 30px;
  margin: 20px 0;
}
.button-group {
  height: 60px;
  line-height: 30px;
}
.button {
  padding: 0 20px;
  line-height: 21px;
  font-size: 14px;
  display: inline-block;
  margin: 0 10px 0 0;
  border: 1px solid #333;
  border-radius: 3px;
  cursor: pointer;
}
.cube {
  width: 180px;
  height: 180px;
  position: relative;
  margin: 100px auto;
  transform-style: preserve-3d;
  -webkit-perspective: 10000000;
}
.opacity-set {
  font-size: 20px;
}



